/**
 * @fileoverview Utilit√°rios para manipula√ß√£o de datas e c√°lculos de escala
 */

/**
 * Formata uma data no formato DD/MM para exibi√ß√£o
 * @param {Date} date - Data a ser formatada
 * @returns {string} Data formatada no padr√£o brasileiro
 * @throws {Error} Se a data for inv√°lida
 */
export const formatDate = (date) => {
  if (!(date instanceof Date) || isNaN(date.getTime())) {
    throw new Error('Data inv√°lida fornecida para formata√ß√£o');
  }

  return date.toLocaleDateString('pt-BR', {
    day: '2-digit',
    month: '2-digit'
  });
};

/**
 * Calcula a diferen√ßa de semanas completas entre duas datas
 * @param {Date} date1 - Data mais recente
 * @param {Date} date2 - Data de refer√™ncia
 * @returns {number} N√∫mero de semanas de diferen√ßa
 * @throws {Error} Se alguma das datas for inv√°lida
 */
export const getWeekDifference = (date1, date2) => {
  if (!(date1 instanceof Date) || isNaN(date1.getTime()) ||
    !(date2 instanceof Date) || isNaN(date2.getTime())) {
    throw new Error('Datas inv√°lidas fornecidas para c√°lculo de diferen√ßa');
  }

  const msPerWeek = 1000 * 60 * 60 * 24 * 7;

  // Alinha ambas as datas para a Segunda e Ter√ßa da semana
  const getmonday_tuesday = (date) => {
    const d = new Date(date);
    const day = d.getDay();
    const diff = (day === 0 ? -6 : 1 - day);
    d.setDate(d.getDate() + diff);
    d.setHours(0, 0, 0, 0);
    return d;
  };

  const monday_tuesday1 = getmonday_tuesday(date1);
  const monday_tuesday2 = getmonday_tuesday(date2);

  return Math.floor((monday_tuesday1 - monday_tuesday2) / msPerWeek);
};

/**
 * Encontra a data da Segunda e Ter√ßa da semana de uma data espec√≠fica
 * @param {Date} date - Data de refer√™ncia
 * @returns {Date} Data da Segunda e Ter√ßa da mesma semana
 * @throws {Error} Se a data for inv√°lida
 */
export const getmonday_tuesdayDate = (date) => {
  if (!(date instanceof Date) || isNaN(date.getTime())) {
    throw new Error('Data inv√°lida fornecida para encontrar Segunda e Ter√ßa');
  }

  const currentDay = date.getDay();
  const diffTomonday_tuesday = (currentDay === 0 ? -6 : 1 - currentDay);
  const monday_tuesdayDate = new Date(date);

  monday_tuesdayDate.setDate(date.getDate() + diffTomonday_tuesday);
  monday_tuesdayDate.setHours(0, 0, 0, 0);

  return monday_tuesdayDate;
};

/**
 * Calcula as datas e √≠ndices dos ciclos para uma semana espec√≠fica
 * @param {Date} currentDate - Data atual para c√°lculo
 * @param {Date} referenceDate - Data de refer√™ncia do sistema
 * @param {Object} scheduleData - Dados da escala
 * @returns {Object} Objeto com datas e √≠ndices calculados
 */
export const calculateWeekCycles = (currentDate, referenceDate, scheduleData) => {
  if (!(currentDate instanceof Date) || isNaN(currentDate.getTime()) ||
    !(referenceDate instanceof Date) || isNaN(referenceDate.getTime())) {
    throw new Error('Datas inv√°lidas fornecidas para c√°lculo de ciclos');
  }

  let referenceDate2 = new Date(currentDate);

  // Se for domingo, usar a pr√≥xima Segunda e Ter√ßa
  if (currentDate.getDay() === 0) {
    referenceDate2.setDate(currentDate.getDate() + 1);
  }

  const monday_tuesdayDate = getmonday_tuesdayDate(referenceDate2);
  const weekDiff = Math.max(0, getWeekDifference(monday_tuesdayDate, referenceDate));

  // Calcula os √≠ndices dos ciclos para cada dia
  const monday_tuesdayCycleIndex = weekDiff % (scheduleData.monday_tuesday?.length || 1);
  const thursday_fridayCycleIndex = weekDiff % (scheduleData.thursday_friday?.length || 1);

  // Calcula a data da quinta e sexta
  const thursday_fridayDate = new Date(monday_tuesdayDate);
  thursday_fridayDate.setDate(monday_tuesdayDate.getDate() + 4);

  return {
    monday_tuesdayDate,
    thursday_fridayDate,
    monday_tuesdayCycleIndex,
    thursday_fridayCycleIndex
  };
};

/**
 * Verifica se uma data √© hoje
 * @param {Date} date - Data a ser verificada
 * @returns {boolean} True se a data for hoje
 */
export const isToday = (date) => {
  if (!(date instanceof Date) || isNaN(date.getTime())) {
    return false;
  }

  const today = new Date();
  return date.getDate() === today.getDate() &&
    date.getMonth() === today.getMonth() &&
    date.getFullYear() === today.getFullYear();
};

/**
 * Obt√©m o nome do dia da semana em portugu√™s
 * @param {number} dayIndex - √çndice do dia (0 = domingo)
 * @returns {string} Nome do dia da semana
 */
export const getDayName = (dayIndex) => {
  const days = [
    'Domingo', 'Segunda e Ter√ßa', 'Ter√ßa-feira', 'Quarta-feira',
    'Quinta-feira', 'Quinta e Sexta', 'S√°bado'
  ];

  return days[dayIndex] || 'Dia inv√°lido';
};

/**
 * Valida se uma data est√° dentro de um intervalo razo√°vel
 * @param {Date} date - Data a ser validada
 * @param {number} yearsRange - Range de anos permitido (padr√£o: 10)
 * @returns {boolean} True se a data for v√°lida
 */
export const isValidDateRange = (date, yearsRange = 10) => {
  if (!(date instanceof Date) || isNaN(date.getTime())) {
    return false;
  }

  const currentYear = new Date().getFullYear();
  const dateYear = date.getFullYear();

  return Math.abs(dateYear - currentYear) <= yearsRange;
};
